.pagination {
  list-style: none;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  border-radius: 2px;
  padding: 5px;
  position: fixed;
  bottom: 40px;
  right: 0px;
}

.pagination li {
  background: rgb(92, 119, 206);
  color: white;
  margin-top: 5px;
  padding: 5px;
  color: #666;
  border-radius: 2px;
  width: 30px;
  height: 30px;
  background: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-weight: 800;
  cursor: pointer;
}

.pagination li:first-child {
  margin-top: 0px;
}

.pagination .active {
  background: rgb(92, 119, 206);
  color: white;
}

.modalWrapper {
  background-color: white;
  border-radius: 5px;
  bottom: auto;
  left: 50%;
  margin-right: -50%;
  position: absolute;
  right: auto;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 150px;
  padding: 15px;
  text-align: center;
  max-width: 400px;
}

.modalWrapper section {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  margin-top: 10px;
}

.modalWrapper section a {
  text-decoration: none;
  color: rgb(92, 119, 206);
  font-size: 30px;
}

.modalWrapper:focus {
  outline: 0;
}

.modalOverlay {
  background-color: rgba(0, 0, 0, 0.4);
  bottom: 0;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 999;
}

.modalWrapper .content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding: 15px;
}

